:root {
  --c-indian-red: hsla(0, 64%, 58%, 1);
  --c-mantis: hsla(102, 54%, 58%, 1);
  --c-corn: hsla(53, 91%, 69%, 1);
  --c-redwood: hsla(14, 32%, 44%, 1);
  --c-independence: hsla(249, 11%, 34%, 1);
  --c-platinum: hsla(0, 0%, 93%, 1);
  --c-cultured: hsla(0, 0%, 95%, 1);
  --c-gunmetal: hsla(210, 10%, 23%, 1);
  --c-gunmetal-t: hsla(210, 10%, 23%, 0.5);
}

@font-face {
  font-family: 'Helvetica';
  src: local('Helvetica'),
    url(./assets/fonts/Helvetica/Helvetica.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Helvetica';
  src: local('Helvetica'),
    url(./assets/fonts/Helvetica/Helvetica-Bold.ttf) format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Helvetica';
  src: local('Helvetica'),
    url(./assets/fonts/Helvetica/Helvetica-Oblique.ttf) format('truetype');
  font-weight: normal;
  font-style: oblique;
}

@font-face {
  font-family: 'Helvetica';
  src: local('Helvetica'),
    url(./assets/fonts/Helvetica/Helvetica-BoldOblique.ttf) format('truetype');
  font-weight: bold;
  font-style: oblique;
}

html {
  height: 100vh;
  width: 100%;
  margin: 0;
  padding: 0;
}

body {
  height: 100% !important;
  width: 100% !important;
  margin: 0;
  padding: 0;
  font-family: Helvetica;
  color: var(--c-independence) !important;
  background-color: var(--c-platinum) !important;
}

#root {
  height: 100% !important;
  width: 100% !important;
  margin: 0;
  padding: 0;
}

.btn-primary {
  background: var(--c-indian-red) !important;
  border-color: var(--c-indian-red) !important;
  color: var(--c-cultured) !important;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary:visited {
  background: var(--c-indian-red) !important;
  border-color: var(--c-indian-red) !important;
  box-shadow: none !important;
}

a,
a:link,
a:visited,
a:active,
a:focus {
  color: var(--c-cultured);
}

a:hover {
  color: var(--c-indian-red) !important;
}

.wrapper-global {
  height: 100%;
  min-height: 100%;
  width: 100%;
  overflow: auto;
}

.wrapper {
  background-color: var(--c-platinum);
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}